import { Component, Input, OnInit } from '@angular/core';
import { HomeService } from 'src/app/core/services/home.service';
import { Content } from 'src/app/models/content';
import { EnumHContentType } from 'src/app/shared/enums.enum';

@Component({
  selector: 'app-home-page',
  templateUrl: './home-page.component.html',
  styleUrls: ['./home-page.component.css']
})
export class HomePageComponent implements OnInit {
  public panel: string = 'slider';
  constructor(private homeService: HomeService) { }
  sliderContent?: Content[];
  whatMakesUsDifferntContent: Content[] = [];
  packages: Content[]=[];

  ngOnInit(): void {
    this.homeService.getAllHomeContent()
    .subscribe(res=>{
      console.log("response: ", res);
      this.sliderContent = res.filter(x=>x.homeIdFK === EnumHContentType.Root);
      this.whatMakesUsDifferntContent = res.filter(x=>x.homeIdFK == EnumHContentType.What_Makes_Us_Different_Home)
      this.packages = res.filter(x=>x.homeIdFK === EnumHContentType.Packages)
      console.log("this.whatMakesUsDifferntContent: ", this.whatMakesUsDifferntContent);
    });
  }


  clickTab(panelName:string) {
    this.panel = panelName;
    }

}
